<template>
  <div>
    <el-row>
      <el-col :span="10">
        <el-tabs v-model="activeLIndex" type="border-card">
          <el-tab-pane :label="item.name" v-for="(item,index) in leftTabs" :key="index">
            <div class="grid-content bg-purple">
              <el-input type="textarea" class="textarea" :rows="25" placeholder="请输入内容" v-model="item.text">
              </el-input>
            </div>
          </el-tab-pane>
        </el-tabs>
        <!-- <el-radio-group v-model="lRadioIndex" class="">
          <el-radio :label="index" v-for="(item,index) in leftTabs" :key="index">{{item.name}}</el-radio>
        </el-radio-group>
        <div class="grid-content bg-purple">
          <el-input type="textarea" class="textarea" :rows="25" placeholder="请输入内容" v-model="leftTextarea">
          </el-input>
        </div> -->
      </el-col>
      <el-col :span="1">
        <div class="dot-tip">To</div>
      </el-col>

      <el-col :span="10">
        <el-tabs v-model="activeRIndex" type="border-card">
          <el-tab-pane :label="item.name" v-for="(item,index) in rightSliceTabs" :key="index" :disabled='item.disabled'>
            <div class="grid-content bg-purple-light">
              <el-input type="textarea" class="textarea" :disabled='isBanInput' :rows="25" placeholder="请在左侧输入内容" v-model="item.text">
              </el-input>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>

</template>

<script>
  import * as htmltoless from 'htmltoless'
  // import * as htmltotext from 'html-to-text'
  // const {
  //   convert
  // } = require('html-to-text');

  import {
    html2wxml
  } from 'htmltowxml';

  export default {
    name: '',
    data() {
      return {
        activeLIndex: 0,
        activeRIndex: 0,
        leftTabs: [{
          name: 'html',
          text: ``,
          to: `less,wxml,text`,
        }],
        rightTabs: [{
          name: 'wxml',
          text: ``
        }, {
          name: 'less',
          text: ``
        }, {
          name: 'text',
          text: ``
        }],
        isBanInput: false,
        leftText: ``,
        allowTo: [], //允许转换的类型
      }
    },
    computed: {
      rightSliceTabs() {
        let tabs = this.rightTabs;
        let to = this.leftTabs[this.activeLIndex].to
        if (to) {
          let tos = to.split(',');
          tabs.forEach(item => {
            item.disabled = !tos.includes(item.name);
          })
          return tabs;
        }
        return [];

      }
    },
    watch: {
      activeLIndex: {
        handler() {
          this.calAllowConverItems();
          this.handleConversion();
        },
        immediate: true,
      },
      activeRIndex: {
        handler() {
          this.handleConversion();
        },
        immediate: true,
      },
      leftTabs: {
        handler(value) {
          if (!value.text) {
            this.rightTabs.forEach(item => {
              item.text = '';
            })
          }
          this.handleConversion();
        },
        // immediate: true,
        deep: true,
      }
    },
    methods: {
      handleConversion() {
        let lefttype = this.leftTabs[this.activeLIndex].name
        let righttype = this.rightTabs[this.activeRIndex].name
        this.leftText = this.leftTabs[this.activeLIndex].text;
        if (lefttype === 'html' && righttype === 'less') {
          this.htmlToLess();
        } else if (lefttype === 'html' && righttype === 'wxml') {
          this.htmlToWxml();
        }
      },
      //htmltoless
      htmlToLess() {
        if (!this.leftText) return
        const less = htmltoless.parse(this.leftText);
        // this.isBanInput = true;
        this.appendRightText(less)
      },
      htmlToWxml() {
        if (!this.leftText) return
        const wxml = html2wxml(this.leftText);
        this.appendRightText(wxml);
      },
      //htmltotext
      htmlToText() {
        const text = convert(html, {
          wordwrap: 130
        });
        this.appendRightText(text);
      },
      appendRightText(text) {
        this.rightTabs[this.activeRIndex].text = text || '格式不正确'
      },
      //计算允许转换的类型
      calAllowConverItems() {
        let to = this.leftTabs[this.activeRIndex].to;
        this.allowTo = to.split(',');
      }

    }

  }
</script>

<style lang="less" scoped>
  .grid-content {
    margin-top: 20px;
  }

  .dot-tip {
    font-weight: bold;
    text-align: center;
    line-height: 3;
  }

  .textarea {
    min-height: 500px;
  }
</style>


